  background-color: var(--ChineseSilver);
}
.important-btn {
  @include button;
  background-color: var(--DarkCyan);
  color: var(--White);
}
.outlined-btn {
  @include button;
  background-color: var(--White);
  color: var(--DarkCyan);
  border: 2px solid var(--DarkCyan);
}
.primary-btn {
  @include button;
  background-color: var(--Gunmetal);
  color: var(--White);
}
.danger-btn {
