$tab-label-height: 40px;
$tab-content-height: 300px;
.tab {
  float: left;

  &__group {
    @include clearfix;
    position: relative;
    height: $tab-label-height + $tab-content-height;
  }

  &__label {
    display: inline-block;
    padding: 0 20px;
    margin-right: 5px;
    font-size: 16px;
    line-height: $tab-label-height;
    font-weight: 500;
    cursor: pointer;

    // Active tab styles
    color: var(--DarkCyan);
    border-bottom: 3px solid var(--DarkCyan);
  }

  &__content {
    position: absolute;
    width: 100%;
    // height: $tab-content-height;
    top: $tab-label-height;
    left: 0;
    right: 0;

    // padding: 20px;s
    // overflow-x: auto;
    background: #fff;
  }

  &__radio {
    display: none;

    &:not(:checked) {
      // Inactive tab styles
      & ~ .tab__label {
        font-size: 16px;
        font-weight: 400;
        color: var(--Gunmetal);
        background: transparent;
        border-bottom: 0;
      }

      & ~ .tab__content {
        display: none;
      }
    }
  }
}

