.radio {
  display: block;
  cursor: pointer;
  user-select: none;
  text-align: left;
  // margin-top: 20px;
  p {
    opacity: 50%;
    margin-top: 0;
    margin-left: 40px;
  }

  input {
    display: none;
    &:disabled {
      display: none;
    }

    & + span {
      display: inline-block;
      position: relative;
      padding-left: 40px;
      font-weight: 500;
      font-size: 16px;
      margin-top: 10px;
      &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 50%;
        margin-right: 5px;
        width: 20px;
        height: 20px;
        border: 1px solid #00838f;
        // box-sizing: border-box;
      }

      &::after {
        content: "";
        display: block;
        width: 10px;
        height: 10px;
        background-color: #00838f;
        position: absolute;
        border-radius: 50%;
        position: absolute;
        top: 6px;
        left: 6px;
        transform: scale(0, 0);
        opacity: 0;
        transition: all 0.3s cubic-bezier(0.64, 0.57, 0.67, 1.53);
      }
    }

    & + h3 {
      display: block;
      position: relative;
      padding-top: 23px;
      margin: 10px;
      font-size: 18px;
      font-weight: 600;
      padding-left: 5px;
      &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 50%;
        margin-right: 5px;
        width: 20px;
        height: 20px;
        border: 1px solid #00838f;
        // box-sizing: border-box;
      }

      &::after {
        content: "";
        display: block;
        width: 10px;
        height: 10px;
        background-color: #00838f;
        position: absolute;
        border-radius: 50%;
        position: absolute;
        top: 6px;
        left: 6px;
        transform: scale(0, 0);
        opacity: 0;
        transition: all 0.3s cubic-bezier(0.64, 0.57, 0.67, 1.53);
      }
    }
    &:checked + span::after,
    &:checked + h3::after {
      opacity: 1;
      transform: scale(1, 1);
    }
  }
}
