.switch_parent {
  .switch {
    position: relative;
    display: inline-block;
    width: 48px;

    height: 24px;
    input {
      opacity: 0;
      width: 0;
      height: 0;
      &:checked + .slider {
        background-color: var(--DarkCyan);
      }
      &:focus + .slider {
        box-shadow: 0 0 1px var(--DarkCyan);
      }
      &:checked + .slider:before {
        -webkit-transform: translateX(24px);
        -ms-transform: translateX(24px);
        transform: translateX(24px);
      }
      &:disabled + .slider:before {
        -webkit-transform: translateX(24px);
        -ms-transform: translateX(24px);
        transform: translateX(24px);
        opacity: 50%;
      }
    }
    .slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 48px;
      height: 24px;
      border-radius: 50px;
      background: rgba(204, 204, 204, 0.4);
      -webkit-transition: 0.4s;
      transition: 0.4s;
      // &:hover {
      //   border: solid var(--DarkCyan);
      // }
      &:before {
        position: absolute;
        content: "";
        width: 16px;
        height: 16px;
        border-radius: 50%;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: 0.4s;
        transition: 0.4s;
      }
      p {
        margin: 0 0 0 60px;
        font-weight: 400;
      }
    }
  }
}

